import React from "react";
import Container from "@mui/material/Container";
import Box from "@mui/material/Box";
import Avatar from "@mui/material/Avatar";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import Typography from "@mui/material/Typography";
import {Copyright} from "../../layouts/MainLayout";
import {StoreCreateForm} from "../Forms/StoreCreateForm";


export const CreateStore = () => {
    return(
        <>
            <Container component="main" maxWidth="xs">
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >
                    <Avatar sx={{m: 1, bgcolor: 'secondary.main'}}>
                        <LockOutlinedIcon/>
                    </Avatar>
                    <Typography component="h1" variant="h1" fontSize={'20px'} pb={2}>
                        Aktywacja konta
                    </Typography>
                    <Typography component="p" variant="body2" sx={{fontSize: '12px', textAlign: 'justify'}}>
                        Zanim bdziesz mg/moga korzysta z naszych usug, wymagana jest aktywacja Twojego konta. W
                        tym celu wysalimy na Twj adres e-mail wiadomo z kodem aktywacyjnym. Prosimy o wprowadzenie
                        otrzymanego kodu w ponisze pole, aby dokoczy proces aktywacji konta. Jeli nie
                        otrzymae/otrzymaa wiadomoci z kodem, upewnij si, e sprawdzie/sprawdzia swoj skrzynk
                        odbiorcz oraz folder ze spamem. W razie problemw z aktywacj, skontaktuj si z nami.
                    </Typography>
                    <StoreCreateForm/>
                </Box>
                <Box p={10}><Copyright/></Box>
            </Container>
        </>
    )
}